import Link from 'next/link';
import { useRouter } from 'next/router';

export default function Index({ post }) {
  if (post.error) {
    return <Error statusCode={404} />;
  }
  const router = useRouter();
  let slug = router.asPath;
  // remove "/"
  slug = slug.substr(1);
  return (
    <div>
      <Link href="/"><a>Goto Index</a></Link>
      <h1>{post.title}</h1>
      <p>This is a post!</p>
    </div>
  );
}

